@charset "utf-8";
@import './default.css';
@import './colorset.css';
@import 'https://hangeul.pstatic.net/hangeul_static/css/nanum-square-round.css';
@font-face {
   font-family: 'Pretendard-Regular';
   src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
   font-weight: 400;
   font-style: normal;
}

html,body{font-size:16px;color:var(--fontColor);}
body{font-family:'Pretendard-Regular','NanumSquareRound',sans-serif,Arial;}

h2,h3{font-family:'NanumSquareRound'}
h2{width:100%;margin-bottom:80px;text-align:center;font-size:48px;}
h3{color:var(--highlightColor);/* color: #88f; */font-size:1.5rem;}
button{display:flex;justify-content:center;align-items:center;border-radius:10px;}

.textHidden{font-size:0px;text-indent:-9999em;overflow:hidden;}

#comp>section{width:100vw;height:100vh;padding:100px 0;background:var(--bgColor);box-sizing:border-box;}
#comp>section:nth-of-type(2n){background:var(--bgColor2);}
#comp>section>h2{display:inline-block;}
#comp>section>div{width:1080px;margin:auto;height:calc(100vh - 200px);}

#comp>.Home{background:linear-gradient(rgba(255,255,255,0.7),rgba(255,255,255,0.3)),url('../img/bgHome4.png') no-repeat;background-size:cover;}

#comp>.Works,#comp>.Works>div,#comp>.Contact,#comp>.Contact>div{height:unset;}


